
import pubfooter from '@/components/pubfooter/pubfooter'
import weixinpay from '@/base/weixinpay/weixinpay'
import tools from '@/common/tools.js'
import axios from 'axios'
import Confirm from 'vux/src/components/confirm/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
name: 'myorder',
data: function() {
return {
totalPage: 1,
allData: [],
confirmShow: false,
nowOrderId: 0,
confirmShowCancel: false,
nowIndex: 0,
confirmShowReceive: false,
nowType: '',
nowPage: 1,
busy: false,
loadingShow: false,
noDataShow: false,
noDataPopShow: true,
isPaying: false
}
},
created() {
this.nowType = this.$route.query.type
this._getOrderData()
},
watch: {
'$route' (to, from) { // 监听路由是否变化
if (to.name === 'myorder') {
console.log('路由变化了', this.$route.query.type)
this.nowType = this.$route.query.type
this.reSetGetData()
this._getOrderData()
}
}
},
directives: {
TransferDom
},
methods: {
goToPayBtn(index) {
let orderId = this.allData[index].order_id
this.$refs.weixinPay.goToPay(orderId)
},
loadMore() {
console.log('重复加载', this.busy)
this.busy = true
this.loadingShow = true

this.timer = setTimeout(() => {

this.nowPage++
this._getOrderData()
}, 1000)
},
toAssess(index) {
this.$router.push({path: 'gotoassess', query: { orderId: this.allData[index].order_id}})
},
seeWuliu(index) {
this.$router.push({path: 'queryexpress', query: { orderId: this.allData[index].order_id}})
},
changeType(type) {
this.nowType = type
this.reSetGetData()
this.$router.push({path: 'myorder', query: { type: this.nowType}})
},
reSetGetData() {
this.nowPage = 1
this.allData = []
this.loadingShow = false
this.noDataShow = false
this.busy = true
this.totalPage = 1
},
onCancelReceive() {
this.confirmShowReceive = false
},
onConfirmReceive() {
console.log('我要开始确认收货了')

let url = '/Mobile/Order/order_confirm.html'
axios.get(url, {
params: {
id: this.nowOrderId
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
this.confirmShowReceive = false
this.allData.splice(this.nowIndex, 1)
tools.showError(res.data.msg)
} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
})
},
receiveOrder(index) {
this.nowOrderId = this.allData[index].order_id
this.confirmShowReceive = true
this.nowIndex = index
},
onConfirmCancel() {
console.log('我要开始取消订单了')

let url = '/Mobile/Order/cancel_order.html'
axios.get(url, {
params: {
id: this.nowOrderId
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
this.confirmShowCancel = false
this.allData.splice(this.nowIndex, 1)
tools.showError(res.data.msg)
} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
})
},
onCancelCancel() {
this.confirmShowCancel = false
},
cancelOrder(index) {
this.nowOrderId = this.allData[index].order_id
this.confirmShowCancel = true
this.nowIndex = index
},
onConfirm() {
console.log('我要开始删除了')

let url = '/Mobile/Order/delete_order.html'
axios.get(url, {
params: {
id: this.nowOrderId
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
this.confirmShow = false
this.allData.splice(this.nowIndex, 1)
tools.showError(res.data.msg)
} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
})
},
onCancel() {
this.confirmShow = false
},
delOrder(index) {
this.nowOrderId = this.allData[index].order_id
this.confirmShow = true
this.nowIndex = index
},
gotoOrderDetails(id) {
this.$router.push({path: 'orderdetail', query: { orderId: id }})
},
_getOrderData() {
console.log(1)
let url = '/Mobile/Order/order_list.html'
axios.get(url, {
params: {
type: this.nowType,
p: this.nowPage
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
/* this.totalPage = res.data.totalPages
this.allData = res.data.order_list */


this.allData = [...this.allData, ...res.data.order_list]
this.totalPage = res.data.totalPages
console.log(this.allData, this.allData.length)
if (this.allData.length > 0) {
this.noDataPopShow = false
} else {
this.noDataPopShow = true
}
console.log('当前页：', this.nowPage, this.totalPage)
if (this.nowPage >= this.totalPage) {
this.busy = true
this.noDataShow = true
this.loadingShow = false
} else {
this.busy = false
}

} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
})
}
},
components: {
pubfooter,
weixinpay,
Confirm,
XDialog
}
}



